.container {
    display: -ms-flexbox;
    display: flex;
    padding: 0.5em 1em;
}

.container2 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 12em;
}

.container3 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.pokemonImageContainer {
    width: 5em;
    height: 5em;
    padding-right: 0.5em;
    margin-top: auto;
}

.pokemonImageContainer img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width:100%;
    max-height:100%;
}

.iconContainer span {
    display: inline-block;
    text-align: center;
    width: 1.5em;
}

.iconContainer img {
    width: 1.2em;
}

.active {
    color: forestgreen;
}

.expired {
    color: red;
}


